@import url('https://fonts.googleapis.com/css?family=Roboto');
html,
body {
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: block;
  width: 100%;
  margin: 0;
  border: 0;
}

*,
::after,
::before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a,
a:visited,
a:hover,
a:active {
  color: #0078ff;
  text-decoration: none;
}

#root {
  min-height: 100vh;
  height: 100%;
}

/* grid */

.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.col {
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  -ms-flex-positive: 1;
      flex-grow: 1;
  max-width: 100%;
}

[class*="col"] {
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}

.justify-content-center {
  -ms-flex-pack: center !important;
      justify-content: center !important;
}

.align-items-start {
  -ms-flex-item-align: center !important;
      align-self: center !important;
}

@media (min-width: 768px) {
  .col-md-4 {
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%;
  }
}

@media (min-width: 992px) {
  .col-lg-3 {
    -ms-flex: 0 0 25%;
        flex: 0 0 25%;
    max-width: 25%;
  }
}

.hidden {
  display: none;
}

/* - */

.buttons {
  text-align: left;
  margin-bottom: 10px;
}

.buttons__mobile {
  display: none;
}

.button {
  margin: 0 10px 10px 0;
  border: 1px solid #d3d3d3;
  background-color: #ffffff;
  border-radius: 25px;
  display: inline-block;
  font-size: 15px;
  text-transform: uppercase;
  padding: 10px 25px;
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button--show {
  border: 1px solid #0078ff;
  color: #0078ff;
}

.app {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow: auto;
  font-family: 'Roboto', sans-serif;
  background-color: #F7F7F7;
}

.app__title {
  font-size: 36px;
  line-height: 1;
  margin-top: 15px;
  margin-bottom: 10px;
  color: #000;
}

.app__description {
  margin-bottom: 15px;
}

.app__content {
  position: relative;
  width: 100%;
  padding: 20px;
  margin: 0 auto;
  min-height: calc(100vh - 80px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.app__text {
  display: inline-block;
  margin-right: 10px;
  font-weight: 300;
}

.app__dashboard {
  width: auto;
  margin-right: -10px;
  margin-left: -10px;
}

.app__dashboard-watcher {
  position: relative;
  width: 100%;
  height: 200px;
  margin-bottom: 30px;
  padding: 15px 10px;
  /* display: none;   */
  display: inline-block;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -o-transition: transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  background-color: #ffffff;
  -webkit-box-shadow: 0 11px 22px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 11px 22px 0 rgba(0, 0, 0, 0.12);
  overflow: hidden;
  border-radius: 8px;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  cursor: pointer;
}

.app__dashboard-watcher:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
}

.chart__state,
.chart__prise,
.chart__name {
  z-index: 10;
}

.chart {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 78px;
  z-index: 5;
}

.chart__name {
  position: absolute;
  top: 10px;
  left: 10px;
  margin-bottom: 10px;
  font-size: 32px;
  line-height: 1;
}

.chart__state {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 4px 6px;
  border-radius: 4px;
  background-color: #fff;
  -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.12);
          box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.12);
}

.chart__prise {
  position: absolute;
  top: 45px;
  left: 10px;
  font-size: 20px;
  font-weight: 500;
}

.chart__change {
  font-size: 14px;
  color: #fff;
}

.chart__indicator {
  position: relative;
  top: -2px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 .4em .4em .4em;
  border-color: transparent transparent #fff transparent;
  display: inline-block;
  margin-right: 10px;
  color: #fff;
}

.lost .chart__prise,
.lost .chart__name {
  color: #ac2a39;
}

.gain .chart__prise,
.gain .chart__name {
  color: #63b556;
}

.lost .chart__state {
  background-color: #ac2a39;
}

.gain .chart__state {
  background-color: #6db362;
}

.lost .chart__indicator {
  border-width: .4em .4em 0 .4em;
  border-color: #fff transparent transparent transparent;
}

.gain .chart__indicator {
  border-width: 0 .4em .4em .4em;
}

.buttons__mobile {
  position: relative;
  padding: 15px;
  cursor: pointer;
}

.icon-arrow {
  position: absolute;
  top: 16px;
  right: 15px;
  width: 14px;
  height: 16px;
  background-image: url(./assets/icon-arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transition: all 400ms ease-in-out;
  -o-transition: all 400ms ease-in-out;
  transition: all 400ms ease-in-out;
}

.buttons__mobile--open .icon-arrow {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
}

/* media init */

@media (max-width: 767px) {
  .app__dashboard-watcher {
    margin-bottom: 15px;
  }
}

@media (max-width: 580px) {
  .app {
    padding-top: 50px;
  }
  .buttons {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12);
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12);
    z-index: 20;
  }
  .buttons__mobile {
    display: block;
    cursor: pointer;
  }
  .buttons__content {
    height: 0;
    overflow: hidden;
    -webkit-transition: height 400ms ease-in-out;
    -o-transition: height 400ms ease-in-out;
    transition: height 400ms ease-in-out;
  }
  .buttons__content--open {
    height: 206px;
  }
  .button {
    float: left;
    width: 33.33333%;
    margin: 0;
    -webkit-transform: scale(0.9);
        -ms-transform: scale(0.9);
            transform: scale(0.9);
    border-radius: 0;
  }
  .app__content {
    margin: 0;
    border-radius: 0;
  }
  .app__text {
    display: block;
  }
}


